@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: 744px) {
    @content;
  }
}

@mixin top-actions {
  position: relative;
  display: grid;
  grid-template-columns: 1fr #{$icon-width} 30px #{$icon-width};
  justify-items: center;
  align-items: center;

  @include onTablet {
    $icon-width: 24px;
    grid-template-columns: 1fr #{$icon-width} 30px #{$icon-width};
  }
}
